.Valute{
  background:var(--form-background);
  width:30%;
  border-radius: 20px;
  padding: 0% 1% 0% 1%;
  text-align:center;
  float:left;
  display:table;
  height:180px;
  margin-top:10px;
  margin-left: 5%;
  box-shadow: 6px 6px 3px 3px black;
  padding-bottom:10px;
}

.Valute__Box{
  display:block;
  text-align: center;
  vertical-align: middle; 
}
.Valute__BankValute{
  text-align:center;
  width:100%;
  margin-top:0px;
  font-size:25px;
}
.Valute__ActualValute{
  margin-top:0px;
  margin-bottom:0px;
}
.Valute__Form{
  width:100%;
  display:inline-block;
  text-align: center;
}
.Valute__ActualyValute{
  text-align:center;
  margin-top:25px;
}
.Valute_ActualyValuteValue{
  font-size:13px;
  margin:0px;
}

.Valute__ActualyValute input{
  margin: 0px 5px 0px 10px;
  border:solid 1.2px var(--border-color);
  color:white;
  padding:3px;
  background:none;
  width:100px;
  background-image: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 0.5) 100%);
  
}
.Valute__ButtonChange{
  font-family: var(--fontfamily-buttons);
  color:var(--text-color);
  background: var(--button-color);
  border:none;
  box-shadow: 3px 3px 2px 2px black;
  transition-duration:1s;
  border-radius:8px;
  margin-top:18px;
  cursor:pointer;
  padding:8px;
  font-size:13px;
}
.Valute__ButtonChange:hover{
  transform:scale(1.1);
}

.Back_defValute{
  background:none;
  border:none;
  cursor:pointer;
  color:rgb(0, 145, 0);
}
.Valute__Validation{
 
  margin: 0px auto;
  margin-top:5px !important;
}

@media only screen and (max-width: 899px) {
  .Valute{
    width:88%;
    margin-bottom:10px;
    height:147.27px;
  }
  .Valute__ActualyValute{
    margin-top:0px;
  }
  .Valute__ActualValute{
    font-size:20px;
  }
}
